@use '../colors' as *;

.user-list {
	min-width: 360px;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	box-sizing: border-box;
	background-color: #1b1a1a;
	color: $heading-color;
	overflow-y: auto;

	h2 {
		padding-bottom: 10px;
		font-size: 2rem;
	}

	.search {
		position: relative;
		padding: 0 1em;
		// margin: 1em 0;

		.search-form {
			input {
				margin: 2em 0;
			}
		}
		.add-users {
			.user-chat {
				display: flex;
				align-items: center;
				gap: 10px;
				padding: 0.5em;
				border-radius: 5px;
				cursor: pointer;
				color: white;

				&:hover {
					background-color: $primary-color;
				}

				img {
					width: 35px;
					height: 35px;
					border-radius: 50%;
					object-fit: cover;
				}

				.user-chat-info {
					span {
						font-size: 1.6rem;
					}
					p {
						font-size: 1.4rem;
						color: lightgray;
					}
				}
			}
		}
	}

	.user-list-item {
		// position: relative;
		display: flex;
		justify-content: flex-start;
		align-items: center;
		padding: 0.5em 0.3em;
		border-radius: 4px;
		// transition: background-color 0.2s;

		&:hover {
			cursor: pointer;
			background-color: #242323;
		}

		.user-name-bio {
			display: flex;
			flex-direction: column;
			.user-display-name {
				font-size: 1.6rem;
			}

			.long-username {
				color: #8b8b8b;
			}

			.user-bio {
				font-size: 0.8rem;
				color: #888a90;
			}
		}
	}

	.user {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 1em;
		margin-top: auto;
		margin-bottom: 0;
		border-top: 1px solid rgba(255, 255, 255, 0.2);

		div {
			display: flex;
			align-items: center;
			.avatar {
				width: 35px;
				height: 35px;
				margin-right: 10px;
			}
		}
	}
}
