@use './colors' as *;
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 62.5%;
	scroll-behavior: smooth;
}


body {
	min-height: 100vh;
	font-family: 'Poppins', sans-serif;
	font-size: 1.6rem;
	background-color: $bg-color;
	color: $text-color;
	overflow: hidden;
}

.container {
	flex-grow: 1;
	padding: 0 60px;
}

a {
	text-decoration: none;
	color: #ddd;
}

.btn {
	padding: 8px 12px;
	border: 1px solid $primary-color;
	border-radius: 8px;
	font-size: 1.6rem;
	text-decoration: none;
	background-color: $primary-color;
	color: #fff;
	transition: 0.3s;

	&:not([disabled]) {
		&:hover {
			border: 1px solid $primary-color;
			cursor: pointer;
			background-color: #1d1d1d;
			color: $primary-color;
		}
	}
}

/* forms */
label {
	display: block;
	margin-bottom: 16px;
}
label span {
	display: block;
	margin-bottom: 6px;
}
input,
textarea {
	box-sizing: border-box;
	width: 100%;
	padding: 12px;
	border: 1px solid #313131;
	border-radius: 8px;
	background-color: #1b1a1a;
	color: #777;

	&:focus {
		outline: none;
	}
}
textarea {
	min-height: 160px;
}
.error {
	color: red;
	background: pink;
	border: 1px solid red;
	border-radius: 4px;
	padding: 8px;
	margin: 10px 0;
}

.text-center {
	text-align: center;
}
